.share {
	&--mobile {
		position: absolute;
		right: 40px;
		top: 3px;
	    @include min-responsive($mdtablet) {
			top: 6px;
			right: 140px;
	    }
	}
	&--nomenu {
		right: 5px;
	}
  //   @include min-responsive($mdtablet) {
  //       position: relative;
  //       float: right;
		// &--mobile {
		// 	position: relative;
		// }
  //   }
	&__title {
		position: relative;
		color: $cl_white;
		cursor: pointer;
		padding: 8px 10px;
		&:before {
			content: "\f1e0";
			// font-family: $font_icon;
			font-size: 20px;
			line-height: 1;
		}
		// &:after {
		//     @include min-responsive($mdtablet) {
		// 		content: " Share";
		// 		font-size: 14px;
		//     }
		// }
	}
	&--inline {
		display: inline-block;
	}
	&--right {
		display: inline-block;
		float: right;
	}
	&--fixed {
		position: fixed;
		//top: 40%;
		bottom: 13px;
		right: 55px;
		z-index: 9;
	}
	&__wrap {
		padding: 0 2px;
		margin: 0;
		display: inline-block;
		height: 40px;
		float: right;
		&--float {
			position: absolute;
			top: 40px;
			right: 0;
			padding: 10px;
			background: rgba(0,0,0,.8);
			min-width: 130px;
			height: auto;
			& .share__link {
				color: #fff;
				background: transparent;
			}
			& .share__link:hover {
				background: transparent;
			}
			& .share__link--fb:hover span {
				color: $cl_fb;
			}
			& .share__link--tw:hover span {
				color: $cl_tw;
			}
			& .share__link--wa:hover span {
				color: $cl_wa;
			}
		}
	}
	&__item {
		padding: 0;
		margin: 4px 0;
		display: block;	
		float: left;	
	}
	&__link {
		width: 36px;
		height: 36px;
		padding: 3px;
		margin: 0;
		line-height: 1;
		font-size: 16px;
		// background: rgba(255, 255, 255, 0.41);
		border-radius: 20px;
		text-align: center;
		vertical-align: middle;
		display: block;
		color: #000;
		// box-shadow: 0px 0px 5px rgba(0,0,0,0.3);
		&:hover {
			color: #fff;
		}
		span {
			width: 30px;
			height: 30px;
			padding: 3px 0px;
			display: block;
			border-radius: 20px;
			background: #fff;
			border: 2px solid transparent;
			line-height: 1.5;
		}
		&--fb:hover {
			color: #fff;
			span {
				background: $cl_fb;
				border: 2px solid $cl_fb;
			}
		}
		&--tw:hover {
			color: #fff;
			span {
				background: $cl_tw;
				border: 2px solid $cl_tw;
			}
		}
		&--wa:hover {
			color: #fff;
			span {
				background: $cl_wa;
				border: 2px solid $cl_wa;
			}
		}
	}
	&__count {
		font-family: $font_body;
		display: inline-block;
		float: right;
		padding: 10px 5px 0 5px;
		display: block;
		font-weight: 400;
		font-size: 10px;
		color: #fff;
		text-align: center;
	}
    &--black{
        color: #000;
    }
	&__total {
		line-height: 13px;
		font-weight: 800;
		font-size: 14px;
		display: block;
	}
}
