.header {
    margin: 0;
    padding: 0;
    position: relative;
    top: 0;
    width: 100%;
    background: #000;
    @include base_transition(all);
    z-index: 10;
        box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.29);
    &--transparent {
        background: rgba(0,0,0,0);
    }
    @include min-responsive($mdtablet) {
        position: fixed;
    }
    &--fixed {
        position: fixed;
    }
    &--absolute {
        position: absolute;
    }
}
.back {
    position: absolute;
    left: 0;
    top: 5px;
    &__link {
        font-size: 22px;
        color: #FFF;
        padding: 7px 10px;
        display: block;
        &:hover {
            color: #fff;
        }
    }
    @include min-responsive($mdtablet) {
        display: none;
    }
}
.logo {
    padding: 5px;
    margin: 0 auto;
    text-align: left;
    top: 3px;
    position: relative;
    @include min-responsive($mdtablet) {
        margin: 0 0 0 10px;
        display: inline-block;
    }
}
.logo-title {
    color: white;
    text-decoration: none;
    font-family: $font_vik2;
    font-size: 36px;
    font-weight: 300;
    letter-spacing: 2px;
    // @include min-responsive($mdtablet) {
    //     font-size: 40px;
    // }

    .title-break {
        text-transform: uppercase;
        display: block;
        font-size: 8px;
        line-height: 10px;
        vertical-align: middle;
    }

    .block-logo {
        float: left;
        border-left: 1px solid white;
        padding-left: 10px;
        margin-top: 3px;
        margin-left: 10px;
    }

    .title-text {
        display: inline-block;
        float: left;
        font-family: $font_vik;
        line-height: 1;
    }
}
.logo--black {
    .logo-title {
        color: #000;
    }
    .logo-title .block-logo {
        border-left: 1px solid #000;
    }
}
.nav {
    font-family: $font_body;
    position: absolute;
    float: none;
    right: 4px;
    top: 7px;
    @include min-responsive($mdtablet) {
        position: relative;
        float: right;
        top: 10px;
        right: 90px;
    }
    &--hide {
        opacity: 0;
    }
    &__menu {
        // font-size: 22px;
        // color: #fff;
        // padding: 7px 10px;
        // display: block;
        width: 36px;
        height: 36px;
        padding: 3px;
        margin: 0;
        line-height: 1;
        font-size: 16px;
        // background: rgba(255,255,255,0.41);
        border-radius: 20px;
        text-align: center;
        vertical-align: middle;
        display: block;
        color: #000;
        // box-shadow: 0px 0px 5px rgba(0,0,0,0.3);
        span {
            width: 30px;
            height: 30px;
            padding: 3px 0px;
            display: block;
            border-radius: 20px;
            background: #fff;
            border: 2px solid transparent;
            line-height: 1.5;
        }
        &:hover {
            color: #000;
        }
        &--lg-hidden {
            @include min-responsive($mdtablet) {
                display: none;
            }
        }
    }
    &__wrap {
        margin: -5px 0 0;
        padding: 0;
        background: rgba(0,0,0,1);
        width: 230px;
        &:before {
            content: '';
            position: absolute;
            right: 0;
            top: 0;
        }
    }
    &__wrap--sm-hidden {
        @include max-responsive($mdtablet) {
            display: none;
        }
    }
    &__wrap--hidden {
        display: none;
    }
    &__item {
        display: block;
        // float: left;
    }
    &__link {
        padding: 7px;
        color: $cl_white;
        text-align: left;
        display: block;
        height: auto;
        vertical-align: bottom;
        font-size: 14px;
        border-bottom: 1px solid #222;
        border-right: 1px solid #222;
        @include min-responsive($grid_desktop) {
            font-size: 14px;
            padding: 14px;
        }
        & span {
            display: block;
            font-size: 11px;
            padding: 0 0 3px 0;
            color: #666;
        }
        &:hover,
        &--active {
            background: #222;
            color: #f5f5f5;
        }
    }
    &__subwrap {
        padding: 0 0 20px;
        @include min-responsive($mdtablet) {
            display: none;
        }
    }
    &__subitem {
        padding: 0 0 0 30px;
    }
    &__sublink {
        padding: 10px 0;
        display: block;
        color: $cl_white;
    }
}
.js-menu--show {
    & .nav__wrap--hidden {
        display: block;
        position: absolute;
        top: 40px;
        right: 0;
        z-index: 3;
        @include min-responsive($mdtablet) {
            top: 46px;
        }
    }
}
